import Vue from 'vue';
import Component from 'vue-class-component';

@Component
export class tagHelper extends Vue {
  createTag() {
    const name = window.prompt('');
    if (name === null) {return;}
    if (name === '') {
      return window.alert('')
        ;
    }
    this.$store.commit('createTag', name);
  }
}

export default tagHelper;
